/* EventGrid.css */
.event-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 20px;
}

@media (max-width: 1000px) {
  .event-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 0px;
  }

  .event-card {
    border: 1px solid #ddd;
    background: linear-gradient(to top, white, blue);
    padding: 10px;
    margin-left: 10px;
    margin-right: 10px;
    border-radius: 10px;
    margin-top: 10px;
    text-align: center;
  }

  .event-card h3 {
    color: black;
    font-size: x-large;
  }

  .event-card p {
    color: black;
    font-size: medium;
  }

  .event-card img {
    max-width: 100%;
    height: 200px;
  }

  /* CustomButton.css */
  .custom-button {
    background-color: blue;
    /* Set your desired background color */
    color: white;
    padding: 10px 20px;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-bottom: 0px;
  }
}

