.App {
    text-align: center;
  }
  
  header {
    background: linear-gradient(to right, rgb(184, 182, 182),white);
    margin-top: 10px;
    padding: 5px;
    margin-left: 10px;
    margin-right: 10px;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 15px;
  }
  h1{
    font-size: xx-large;
    margin-left: 10px;
    color: black;
  }
  
  
  .logo {
    max-width: 50px; 
    border-radius: 10px;/* Adjust the max-width as needed */
    margin: 0;
  }

  .logo img{
    width: 100%;
  }



  
  nav {
    ul {
      list-style: none;
      display: flex;
      margin: 0;
      padding: 0;
    }
  
    a {
      text-decoration: none;
      margin: 0 15px;
      cursor: pointer;
      font-size: 16px;
      color: black;
      font-style: oblique;
    }
  }
  
  .menu-icon {
    display: none;
    flex-direction: column;
    cursor: pointer;
  
    .bar {
      height: 3px;
      width: 35px;
      background-color: black;
      margin: 5px 0;
    }
  }
  
  @media (max-width: 768px) {
    .menu-icon {
      display: flex;
      .bar {
        height: 2px;
        width: 30px;
        background-color: black;
        margin: 3px 0;
      }
    }

    header {
      background: linear-gradient(to right, rgb(184, 182, 182),white);
      margin-top: 20px;
      padding: 5px;
      margin-left: 10px;
    margin-right: 10px;
      height: 40px;
      color: white;
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-radius: 15px;
    }
    h1{
      font-size: x-large;
      margin-left: 10px;
      color: black;
    }
    
    nav {
      flex-direction: column;
      background: linear-gradient(to top, purple,white);
      border-radius: 10px;
      float: right;
      width: 50%;
      right: 20px;
      text-decoration: none;
      position: absolute;
      top: 90px;
    }
    nav.show {
        display: flex;
    }
      
    nav.hide {
        display: none;
    }
  
    nav ul {
      flex-direction: column;
      align-items: center;
      
    }
  
    nav a {
      margin: 10px 0;
    }
  }