/* TechnicalEvent.css */
.technical-event-container {
    max-width: 800px;
    margin: auto;
    margin-top: 30px;
    background: white;
    border-radius: 10px;
    padding: 20px;
    text-align: center;
  }
  
  
  section {
    margin-bottom: 20px;
  }
  
  h2 {
    color: #4caf50;
    text-align: start;
  }
  
  ul {
    list-style-type: none;
    padding: 0;
  }
  
  button.register-button {
    background-color: #4caf50;
    color: white;
    padding: 10px 20px;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button.register-button:hover {
    background-color: #45a049;
  }

  p{
    font-weight: bold;
    text-align: start;
  }
